import React, { Component } from 'react';
import './minecraft.css'

class ButtonMine extends React.Component{
    render(){
        return(
            <div>
                <button className="getMine"><font color="white">Get Minecraft</font></button>   
                
                <font className="texto" color="white">
                    <p>or <a href="/web/20140316170517/https://minecraft.net/demo">play the demo</a> </p>
                    <p> Already bought the game?<br></br><a href="/web/20140316170517/https://minecraft.net/download">Download it here</a></p>       
                </font>     
                
            </div>
        )
    }
}



export default ButtonMine;